html{
    height: 100vh;
}
.soundcloud{
    position: absolute;
    top:75px;
    left: 205px;
    display:inline-block;
}
body {
    background: #323131;
    background-repeat: no-repeat;
    background-attachment: fixed;
    height: 100%;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.ball{
    border-radius: 50%;
    position: absolute;
    z-index: -5;
}
.ball3{
    transform: translate(200px, -50px);
    height: 300px;
    width: 300px;
    background:linear-gradient(120deg, #ff8800, #ff3300);}
.ball2{
    transform: translate(-160px, 100px);
    height: 150px;
    width: 150px;
    background:linear-gradient(120deg, #ff8800, #ff3300);}
.ball1{
    transform: translate(-200px, -140px);
    height: 240px;
    width: 240px;
    background:linear-gradient(120deg, #ff8800, #ff3300);}

#cardWrap{
    display: flex;
    justify-content: center;
    align-items: center;
    transform-origin: center;
}

.cardContent{
    position: absolute;
    height: 100%;
    width: 100%;
    border-radius: 30px;
    box-sizing: border-box;
}
.content{
    font-family: Roboto;
    color: rgba(255,255,255, 1.0);
    text-align: center;
}

/* frosted glass effect */
.card {
    height: 250px;
    width: 400px;
    background: rgb(255,255,255);
    background: linear-gradient(80deg, rgba(255,255,255,0.1) 0%, #0d324d 100%);
    backdrop-filter: blur(2px);
    -webkit-backdrop-filter: blur(2px);
    border: 2px outset rgba(255, 255, 255, 0.3);
    box-shadow: 0px 1px 24px -1px rgba(0, 0, 0, 0.2);
}

/* grain/noise effect */
.card::after {
    content:"";
    position: absolute;
    width: 400px;
    height: 250px;
    border-radius: 30px;
    opacity: 0.5;
    background-image: url(data:image/png;base64,);
}
.spin {
    position: absolute;
    top:214px;
    left: 240px;
    animation: App-logo-spin infinite 2s linear;
    display:inline-block;
}
.notInSoundcloud{
    width: 330px;
}
@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
